import React from 'react';
import './style.scss';
import config from '../../config/constants'

/**
 * @props data {Object} Photo details
 * @props size {text}   Size of photo to be fetched
 */
class Card extends React.PureComponent {
  constructor(props){
    super();
    this.state = {
      showOverlay: false,
      card: props.data
    }
    this.handleMouseEnter = this.handleMouseEnter.bind(this);
    this.handleMouseLeave = this.handleMouseLeave.bind(this);
  }

  // ******** LIFECYCLE METHODS *********** //


  render() {
    // console.log('Card -> Render');
    const c = this.state.card;
    if(this.props.size === config.PHOTO_MOBILE ? true: false){
      return (
        <div className="card card--mobile" >
          <span className="user">
            <img src={c.user['profile_image'].small} alt={c.user.username}/>

            <div className="user__details">
              <span>{c.user.name}</span>
              { c.user.location && 
                <span>{c.user.location}</span>
              }
            </div>
          </span>

          <img src={c.urls[this.props.size]} alt={c.id}/>

          <div className="top__section">
            <span className="pill">{c.likes} <span className="red">&#10084;</span></span>
          </div>
        </div>
        )
    }else{
      const showOverlay = {opacity: this.state.showOverlay ? 1: 0}
      return (
        <div className="card" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>
  
          <img src={c.urls[this.props.size]} alt={c.id}/>

          <div className="card__overlay" style={showOverlay}>
            <div className="card__overlay__content">
              <div className="top__section">
                <span className="pill">{c.likes} <span className="red">&#10084;</span>	</span>
                <span className="pill"> + Add to Fav </span>
              </div>
              <div className="bottom__section">
                <span className="user">
                  <img src={c.user['profile_image'].small} alt={c.user.username}/>
                  <span>{c.user.name}</span>
                </span>
              </div>
            </div>
          </div>

        </div>
        )
    }
    
  }

  // ******** CUSTOM METHODS *********** //

  handleMouseEnter(evt){
    this.setState({
      showOverlay: !this.state.showOverlay
    });
  }

  handleMouseLeave(){
    this.setState({
      showOverlay: !this.state.showOverlay
    });
  }
};
  
export default Card;